import React, { useState } from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Highlighter from "react-highlight-words";

export default function TextForm(props) {
    
    const [text, settext] = useState("");
    const [original, setoriginal] = useState(text);
    const [isCopied, setIsCopied] = useState(false);
    const [search, setsearch] = useState('');

    const onCopyText = () => {
      setIsCopied(true);
      setTimeout(() => {
        setIsCopied(false);
      }, 1000);
    };
    const onchange=(event)=>{

        settext(event.target.value);
        setoriginal(event.target.value);
    }
    const changetouppercase=()=>{

        settext(text.toUpperCase());
    }
    const changetooriginal=()=>{
        settext(original);
    }
    const changetolowercase=()=>{
        settext(text.toLowerCase());
    }
    const removeextraspace=()=>{
        let data1=text.trim().split(' ').filter(word=>word!=='');
        settext(data1.join(' '));
    }
    const makefirstlettercapforeachsentence=()=>{
        let sentence = text.split('.'), temp=[];
    
        for(var i=0; i<sentence.length; i++)
        {
            var j=0;
            while(sentence[i][j] === ' '){
                j++;
            }  
            temp.push(sentence[i].charAt(j).toUpperCase()+sentence[i].substring(j+1));
            
        }
        settext(temp.join(". "));
    
    }
    const clear = ()=>{
        settext('');
    }
    const highlight=(event) =>{
        setsearch(event.target.value);
        var inputText = document.getElementById("inputText");
        var innerHTML = inputText.innerHTML;
        var index = innerHTML.indexOf(text);
        if (index >= 0) { 
         innerHTML = innerHTML.substring(0,index) + "<span class='highlight'>" + innerHTML.substring(index,index+text.length) + "</span>" + innerHTML.substring(index + text.length);
         inputText.innerHTML = innerHTML;
        }
      }
    // const italisizewithinquotes=()=>{
    //     let temp = "";
    //     for(let i in text){
    //         if(text[i]==='\"'|| text[i]==='\'')
    //         {
    //             let j = i+1;
    //             while(text[j]==='\"'|| text[j]==='\'')
    //                 temp+=text[j];
    //             let newvar = temp.italics();
    //             settext(text.substring(0,i)+newvar+text.substring(j,text.length));
    //         }
    //     }
    // }
    return (
        <>
            <div className='container'>
            
                <div className="mb-5">
                    <h1>Enter/Paste some text here</h1>
                    {(text||original)&&<button className="btn btn-secondary my-2" onClick={changetooriginal}>Reset Original</button>}
                    
                    <textarea className={`form-control bg-${props.mode==='dark'?'secondary':'light'}`} value={text} onChange={onchange} id="textForm" rows="8"></textarea>

                    <button className="btn btn-secondary mx-1 my-2" color='red' onClick={changetouppercase}>Change to Upper case</button>
                    
                    <button className="btn btn-secondary mx-1 my-2" onClick={changetolowercase}>Change to lower case</button>
                    <button className='btn btn-secondary mx-1 my-2' onClick={removeextraspace}>Remove extra space</button>
                    <button className='btn btn-secondary mx-1 my-2' onClick={makefirstlettercapforeachsentence}>Correct capilalization</button>
                    <button className='btn btn-secondary mx-1 my-2' onClick={clear}>Clear</button>
                    {/* <button className='btn btn-secondary mx-1 my-2' onClick={italisizewithinquotes}>Italisize within quotes</button> */}
                </div>
                {/* <textarea className={`form-control bg-${props.mode==='dark'?'secondary':'light'}`} value={search} onChange={highlight} id="" rows="1">input to search</textarea> */}

                <CopyToClipboard text={text} onCopy={onCopyText}>
                    <div className="copy-area my-3 mx-1">
                    <button> {isCopied?"Copied":"Copy to Clipboard"}</button>  
                    </div>
                </CopyToClipboard>
                <div className='info my-2'>
                    <li> No of characters: {text.length}</li>
                    <li> No of words: {text.length?text.split(' '||'  ').length:0} </li>
                </div>
                
            </div>
            
        
        </>
        
    )
}
